.data-processing__checkbox-label{width:100%;display:block;margin:0;margin-bottom:30px;cursor:pointer;user-select:none;position:relative}.data-processing__checkbox-label .data-processing__checkbox{position:absolute;z-index:-1;opacity:0;display:block;width:0;height:0}.data-processing__checkbox-label .data-processing__checkbox-text{max-width:400px;width:100%;display:inline-block;position:relative;padding:0 0 0 31px}.data-processing__checkbox-label .data-processing__checkbox-text:before{content:"";display:inline-block;width:22px;height:22px;position:absolute;left:0;top:0;transition:background .3s ease;background:url(../../../../images/сheckbox.svg) 0 0 no-repeat}.data-processing__checkbox-label .data-processing__checkbox:checked+.data-processing__checkbox-text:before{background:url(../../../../images/checkbox-active.svg) 0 0 no-repeat}.data-processing__checkbox-text{text-transform:uppercase;color:rgba(0,0,0,.5);font-family:"Inter",sans-serif;margin:0;font-size:12px;font-weight:500;line-height:15.6px;text-align:left}